@import url("https://fonts.googleapis.com/css2?family=Outfit:wght@100..900&display=swap");

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: "outfit", 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

p {
  font-size: 1.2rem; /* Adjusted for smaller screens */
}

h1 {
  font-size: 4rem; /* Adjusted for smaller screens */
}

.logo {
  padding: 10px; /* Reduced padding for smaller screens */
  text-align: center;
}

.logo h2 {
  font-size: 2rem;
  font-weight: 500;
  color: #ebeced;
}

.app {
  width: 100%;
  min-height: 100vh; /* Adjusted for full height */
  position: relative;
  background-color: rgba(0, 0, 0, 0.4);
  color: #ebeced;
}

.app::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url(./assets/pexels-stywo-1261728.jpg) no-repeat center center/cover;
  z-index: -1;
}

.app .input {
  position: relative;
  text-align: center;
  padding: 0.8rem;
}

.app input {
  padding: 0.5rem 2rem 0.5rem 1rem; /* Adjusted padding for the input and button */
  font-size: 1rem;
  border-radius: 20px; /* Adjusted border radius */
  border: 1px solid rgba(255, 255, 255, 0.8);
  background-color: rgba(255, 255, 255, 0.1);
  outline: none;
  color: #ebeced;
  width: 100%; /* Ensure input spans full width */
}

.app .search-button {
  position: absolute;
  right: 15px; /* Adjust button position */
  top: 50%;
  transform: translateY(-50%);
  background-color: #ebeced; /* Example button styling */
  border: none;
  color: #777;
  padding: 0.5rem 1rem;
  border-radius: 20px; /* Same as input */
  cursor: pointer;
}
.app .search-button:hover {
  background-color: #d1d1d1; /* Example hover styling */
}

::placeholder {
  color: #ebeced;
}

.container {
  width: 90%; /* Adjusted for responsiveness */
  max-width: 700px; /* Maximum width for larger screens */
  position: relative;
  padding: 0 1rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  top: 5%;
  margin: auto;
  backdrop-filter: blur(8px);
  border-radius: 15px;
  border: 1px solid #ebeced;
}

.app .top {
  width: 100%;
  margin: 1rem auto;
}

.app .description {
  position: relative;
  right: -90%;
  transform-origin: 0 0;
  transform: rotate(269.5deg);
}

.app .bottom {
  display: flex;
  justify-content: space-evenly;
  text-align: center;
  align-items: center;
  width: 100%;
  margin: 1rem auto;
  padding: 1rem;
  border-radius: 12px;
  background-color: rgba(255, 255, 255, 0.2);
}

.bold {
  font-weight: 700;
}
